nav {
  background-color: var(--green);
}

body {
  flex-direction: column;
  min-width: 100vw;
  height: 100vh;
  display: flex;
}

main {
  background-color: var(--green);
  padding-left: var(--x-padding);
  padding-right: var(--x-padding);
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  justify-content: space-between;
  width: 100%;
  display: flex;
  overflow-y: auto;
}

#gdpr-content-box {
  place-self: center;
  width: 650px;
  margin-bottom: 1vh;
}

@media screen and (width <= 700px) and (orientation: portrait) {
  #gdpr-content-box {
    place-self: unset;
    width: unset;
    max-width: 650px;
  }
}

header h1 {
  margin-top: 1em;
  font-size: max(2rem, min(7vw, 3rem));
}

.desktop-title {
  display: block;
}

.mobile-title {
  display: none;
}

@media screen and (width <= 450px) and (orientation: portrait) {
  .desktop-title {
    display: none;
  }

  .mobile-title {
    display: block;
  }
}

p {
  margin-top: 24px;
  font-size: max(1.2rem, min(3vw, 1.2rem));
}

.footer p {
  font-size: max(1.2rem, min(5vw, 2rem));
  font-weight: 600;
}

ul {
  margin-bottom: 1em;
}

li {
  color: #fff;
  font-optical-sizing: auto;
  margin-bottom: 12px;
  font-family: Work Sans, sans-serif;
  font-size: max(1.2rem, min(3vw, 1.2rem));
  font-style: normal;
  font-weight: 400;
}

.stripes {
  width: 100vw;
  height: 72px;
  margin-top: 24px;
  margin-bottom: 24px;
  margin-left: calc(0px - var(--x-padding));
  background: url("stripes.c032f86a.svg");
}
